import React, { Component } from 'react';
import Dialog from 'material-ui/Dialog';
import SvgIcon from 'material-ui/SvgIcon';
import Checkbox from 'material-ui/Checkbox';
import FlatButton from 'material-ui/FlatButton';
import ContentArchive from 'material-ui/svg-icons/content/archive.js';
import ActionSearch from 'material-ui/svg-icons/action/search.js';
import cocktails from './cocktails.json';

class Stock extends Component {
  constructor(props) {
    super(props);
    this.state = {open: false};
    this.checkboxes = React.createRef();
  }
  handleOpen () {
    this.setState({open: true});
  }
  handleClose () {
    this.setState({open: false});
  }
  saveStock () {
    let stockMaterials = Array.from(this.checkboxes.current.querySelectorAll(':checked')).map(function(x) {
      return x['value'];
    });
    localStorage.setItem('stockMaterials', JSON.stringify(stockMaterials));
    this.setState({open: false});
  }
  searchStock () {
    let stockMaterials = JSON.parse(localStorage.getItem('stockMaterials'));
    let available = cocktails.filter(function(cocktail) {
      return cocktail.material.every(function(material) {
        return stockMaterials.includes(material);
      });
    });
    console.log(available);
    this.props.setCocktails(available);
  }
  materials () {
    let stockMaterials = localStorage.getItem('stockMaterials');
    return this.props.materials.sort().map(function(material) {
      if (!stockMaterials.includes(material)) return <Checkbox label={material} key={material} value={material}/>;
      return <Checkbox label={material} key={material} defaultChecked={true} value={material}/>;
    });
  }
  render() {
    const actions = [
      <FlatButton
        label="Cancel"
        primary={true}
        onClick={this.handleClose.bind(this)}
      />,
      <FlatButton
        label="Submit"
        primary={true}
        onClick={this.saveStock.bind(this)}
      />,
    ];
    return (
      <div className="Stock">
        <ContentArchive onClick={this.handleOpen.bind(this)}/>
        <ActionSearch onClick={this.searchStock.bind(this)}/>
        <Dialog open={this.state.open} onRequestClose={this.handleClose.bind(this)}
          modal={true} actions={actions} bodyStyle={{'overflow': 'scroll'}}>
          <div ref={this.checkboxes}>
            {this.materials()}
          </div>
        </Dialog>
      </div>
    );
  }
}

export default Stock;



