import React, { Component } from 'react';
import Thumb from './Thumb.js';
import './App.css';
import cocktails from './cocktails.json';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import TextField from 'material-ui/TextField';
import Paper from 'material-ui/Paper';
import Stock from './Stock.js';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {cocktails: cocktails};
    this.requirement = React.createRef();
    if(localStorage.getItem('stockMaterials') == undefined) {
      localStorage.setItem('stockMaterials', JSON.stringify([]));
    }
  }
  setCocktails (cocktails) {
    this.setState({cocktails: cocktails});
  }
  cocktailThumbs () {
    return this.state.cocktails.slice(0,10).map(function(x) {
      return <Thumb key={x.title} img={x.img[0]} materials={x.material} name={x.title} ml={x.ml}></Thumb>;
    });
  }
  refineCocktails () {
    let newCocktails = cocktails.filter(function(cocktail) {
      return cocktail.material.includes(this.requirement.current.input.value);
    }, this);
    this.setState({cocktails: newCocktails});
  }
  allMaterials () {
    return Array.from(cocktails.reduce(function(list, cocktail) {
      return new Set([...list, ...cocktail.material]);
    }, new Set([])).values());
  }
  render() {
    return (
      <MuiThemeProvider>
        <div className="App">
          <div>
            <TextField hintText="material" ref={this.requirement} onChange={this.refineCocktails.bind(this)} />
          </div>
          <div className="Thumbs">
            {this.cocktailThumbs()}
          </div>
          <Paper zDepth={2} className="Footer">
            <Stock setCocktails={this.setCocktails.bind(this)} materials={this.allMaterials()}/>
          </Paper>
        </div>
      </MuiThemeProvider>
    );
  }
}

export default App;



