import React, { Component } from 'react';

class DialogCocktail extends Component {
  materials() {
    let arr = this.zip(this.props.materials, this.props.ml);
    return arr.map(function(x){
      return(<div>{x[0]}: {x[1]}</div>);
    });
  }
  zip(arr1, arr2) {
    return arr1.map(function(x, index){
      return [arr1[index], arr2[index]];
    });
  }
  render() {
    return (
      <div className="DialogCocktail">
        <div>{this.props.name}</div>
        {this.materials()}
      </div>
    );
  }
}

export default DialogCocktail;



