import React, { Component } from 'react';
import './Thumb.css';
import {Card, CardActions, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';
import Dialog from 'material-ui/Dialog';
import DialogCocktail from './DialogCocktail.js';

class Thumb extends Component {
  constructor(props) {
    super(props);
    this.state = {open: false};
  }
  handleOpen () {
    this.setState({open: true});
  }
  handleClose () {
    this.setState({open: false});
  }
  render() {
    return (
      <React.Fragment>
        <Card className="Thumb" onClick={this.handleOpen.bind(this)}>
          <CardMedia style={{'textAlign': 'center'}} overlay={<CardTitle title={this.props.name}/>}>
            <img className="ThumbImg" src={this.props.img} alt={this.props.name} />
          </CardMedia>
        </Card>
        <Dialog open={this.state.open} onRequestClose={this.handleClose.bind(this)}>
          <DialogCocktail name={this.props.name} materials={this.props.materials} ml={this.props.ml} />
        </Dialog>
      </React.Fragment>
    );
  }
}

export default Thumb;



